<template>
    <div class="bussinessChartPopUps_box">
        <div id="bussinessChartPopUps" ref="bussinessChartPopUps"></div>
        <!-- 当前价格 -->
        <div :style="{top: startY+'px', left: startX + 'px'}" class="position-a endBorders">
            <div>{{1424.255455}}</div>
        </div>
        <!-- 定位logo -->
        <div :style="{top: 10+'px', left: 30 + 'px'}" class="position-a logo_box"></div>
    </div>
</template>

<script>
export default {
    name: "bussinessChartPopUps",
    props: {},
    data() {
        return {
            startY: 0,
            startX: 0,
            DataList: [], //数据
            // placeAnOrderList: [[1575887401000, 1], [1575887403000, 1]] //下单标注点
            placeAnOrderList: [] //下单标注点
        };
    },
    computed: {
        markPointData() {
            let obj = [];
            this.placeAnOrderList.forEach(item => {
                let placeAnOrder = {
                    //下单的点
                    symbolSize: "30",
                    label: {
                        normal: {
                            formatter: function(param) {
                                return param != null
                                    ? Math.round(param.data.yAxis)
                                    : "";
                            }
                        },
                        fontSize: 10
                    },
                    xAxis: item[0],
                    yAxis: item[1]
                };
                obj.push(placeAnOrder);
            });
            let endCallout = {
                //终点标注点
                symbol: "circle",
                symbolSize: "10",
                label: {
                    normal: {
                        show: false
                    },
                    fontSize: 10
                },
                itemStyle: {
                    color: "#fff"
                },
                xAxis: this.DataList[this.DataList.length - 1][0],
                yAxis: this.DataList[this.DataList.length - 1][1]
            };
            obj.push(endCallout);
            return obj;
        },
        markLineData() {
            let arr = [["#439957", 11], ["red", 1]];
            let brr = [];
            arr.forEach(item => {
                let lineStyleObj = {
                    lineStyle: {
                        type: "solid",
                        color: {
                            type: "linear",
                            x: 0,
                            y: 0,
                            x2: 0,
                            y2: 1,
                            colorStops: [
                                {
                                    offset: 0,
                                    color: "#273042" // 0% 处的颜色
                                },
                                {
                                    offset: 1,
                                    color: item[0] // 100% 处的颜色
                                }
                            ],
                            global: false // 缺省为 false
                        },
                        width: 1
                    },
                    xAxis: this.DataList[this.DataList.length - item[1]][0]
                };
                brr.push(lineStyleObj);
            });
            return brr;
        }
    },
    created() {
        let arr = [];
        let time = 1575887400000;
        for (let index = -1; index < 60; index++) {
            arr.push([time, Math.ceil(Math.random() * 10)]);
            time += 1000;
        }
        this.DataList = arr;
    },
    mounted() {
        var echarts = require("echarts");
        var myChart = echarts.init(
            document.getElementById("bussinessChartPopUps")
        );
        window.addEventListener("resize", function() {
            myChart.resize();
        });
        function add0(m) {
            return m < 10 ? "0" + m : m;
        }
        let option = {
            animationDuration: 0,
            backgroundColor: "#273042",
            title: {
                text: this.$t("main.guideMap.subtext"),
                left: "center",
                top: "10",
                textStyle: {
                    color: "#fff",
                    fontSize: 16
                }
            },
            grid: [
                {
                    left: "10%",
                    bottom: "10%",
                    top: "20%",
                    right: "10%"
                }
            ],
            xAxis: {
                type: "time",
                boundaryGap: false,
                splitLine: {
                    show: false
                },
                minInterval: 1,
                axisLine: {
                    lineStyle: {
                        color: "#333",
                        width: 1
                    }
                },
                axisLabel: {
                    textStyle: {
                        fontSize: 10,
                        color: "#a0a0c1"
                    }
                },
                splitNumber: 20,
                splitArea: false
            },
            yAxis: {
                type: "value",
                min: function(value) {
                    return value.min - 1;
                },
                max: function(value) {
                    return value.max + 1;
                },
                axisLine: {
                    lineStyle: {
                        color: "#333",
                        width: 1
                    }
                },
                splitLine: {
                    show: false
                },
                axisLabel: {
                    formatter: function(value, index) {
                        return value.toFixed(2);
                    },
                    textStyle: {
                        fontSize: 10,
                        color: "#a0a0c1"
                    }
                }
            },
            series: [
                {
                    type: "line",
                    symbol: "none",
                    data: this.DataList,
                    areaStyle: {
                        //线性填充区域颜色
                        normal: {
                            color: new echarts.graphic.LinearGradient(
                                0,
                                0,
                                0,
                                1,
                                [
                                    {
                                        offset: 0,
                                        color: "#DDDDDD" // 0% 处的颜色
                                    },
                                    {
                                        offset: 1,
                                        color: "rgba(0,0,0,0)" // 100% 处的颜色
                                    }
                                ]
                            )
                        }
                    },
                    lineStyle: {
                        //折现的颜色设置}
                        normal: {
                            color: "#fff",
                            width: 1
                        }
                    },
                    markLine: {
                        //标注线的设置
                        silent: true,
                        symbol: "none",
                        label: {
                            show: true,
                            normal: {
                                show: true,
                                textStyle: {
                                    fontSize: 10,
                                    color: "#439957"
                                },
                                formatter: function(params) {
                                    let time = new Date(params.value);
                                    var h = time.getHours();
                                    var mm = time.getMinutes();
                                    var s = time.getSeconds();
                                    return (
                                        add0(h) + ":" + add0(mm) + ":" + add0(s)
                                    );
                                }
                            }
                        },
                        data: this.markLineData
                    },
                    markPoint: {
                        //标注点的设置
                        silent: true,
                        data: this.markPointData
                    }
                }
            ]
        };
        myChart.setOption(option);
        let _points = myChart.getViewOfSeriesModel(
            myChart.getModel().getSeries()[0]
        )._points;
        this.startY = _points[_points.length - 1][1] - 8;
        this.startX = _points[_points.length - 1][0] + 6;
    },
    watch: {},
    methods: {
    },
    components: {}
};
</script>
<style scoped lang="scss">
.bussinessChartPopUps_box {
    width: 100%;
    position: relative;
    #bussinessChartPopUps {
        width: 780px;
        height: 300px;
    }
    @media screen and (max-width: 768px) {
        #bussinessChartPopUps {
            display: none;
        }
    }
    .position-a {
        position: absolute;
    }
    .endBorders {
        z-index: 11;
        text-align: center;
        width: 72px;
        div {
            width: 100%;
            background: #494869;
            z-index: 3;
            color: #fff;
            font-size: 10px;
            line-height: 16px;
            white-space: nowrap;
        }
    }
    .logo_box {
        width: 150px;
        height: 26px;
        background: url("../assets/okbix-logo-small.png") no-repeat;
        background-size: auto 100%;
    }
}
@media screen and (max-width: 768px) {
}
</style>
