<template>
    <table>
        <thead>
            <tr>
                <th>mm-dd-yyyy</th>
                <th>{{$t('main.OrderRecord.Position')}}</th>
                <th>{{$t('main.OrderRecord.RemainingTime')}}</th>
            </tr>
        </thead>
        <tbody>
            <template v-for="(item, i) in tradeItems">
                <tr :key="i">
                    <td class="index-type" colspan="3">{{item.marketCode}}</td>
                </tr>
                <tr :key="i+'a'">
                    <td class="date">{{item.createTime}}</td>
                    <td>
                        <div v-bind:class="item.marketPrice - item.nowPrice > 0  ? 'direction up' : 'direction down'">
                            {{item.marketPrice - item.nowPrice > 0  ? $t('main.OrderRecord.up') : $t('main.OrderRecord.down')}}
                        </div> {{item.tradePrice / 100}}</td>
                    <td class="time-remaining">
                        {{item.timeRemaining}} 
                        <div class="icon-container" @click="showDetails(item)"><i v-if="!item.showDetails" class="el-icon-arrow-down"></i></div>
                    </td>
                </tr>
                <tr :key="i+'b'" v-bind:style="{'border-bottom': history ? '1px solid #787B89' : 'none'}"">
                    <td colspan="3" class="details">
                        <div class="price-difference">
                            <div class="previous-amount">{{item.marketPrice}}</div>
                            <i class="el-icon-right"></i> <i v-if="!history" class="el-icon-time"></i> 
                            <span v-bind:class="item.marketPrice - item.nowPrice > 0  ? 'up' : 'down'">{{item.nowPrice}}</span>
                        </div>
                        <div v-if="item.showDetails">
                            <div class="details-split">
                                <div>{{$t('main.OrderRecord.Time')}}: 1m</div>
                                <div v-if="history">{{$t('main.OrderRecord.HoldTime')}}: {{item.holdTime}} <div class="icon-container"></div></div>
                            </div>
                            <div class="details-split">
                                <div>{{$t('main.OrderRecord.Start')}}: {{item.tradeTime}}</div>
                                <div>{{$t('main.OrderRecord.End')}}: {{item.end}} <div class="icon-container"></div></div>
                            </div>
                            <div class="id details-split">
                                <div>{{$t('main.OrderRecord.OrderID')}}: {{item.tradeId}}</div>
                                <i class="el-icon-arrow-up" @click="showDetails(item)"></i>
                            </div>
                        </div>
                        <div v-if="!history" class="progress-bar-container">
                            <div class="progress-bar" v-bind:style="{width: item.percentTimeLeft*100 + '%'}"></div>
                        </div>
                    </td>
                </tr>
            </template>
        </tbody>
    </table>
</template>

<script>

export default {
    name: "TradeTable",
    components: {
    },
    data() {
        return {
            
        }
    },
    props: {
        tradeItems: {
            type: Array,
            default: []
        },
        history: {
            type: Boolean,
            default: false
        }
    },
    computed: {

    },
    created() {},
    destroyed() {
    },
    mounted() {
    },
    methods: {
        showDetails(item){
            item.showDetails = !item.showDetails;
            this.$forceUpdate();
        },
    }
};
</script>

<style lang='scss' scoped>
    table {
        margin-top: 20px;
        font-size: 13px;
        color: #DDDEE5;
        width: 100%;
        i {
            cursor:pointer;
        }
        tr {
            td, th {
                
                text-align: center;
                &:first-of-type {
                text-align: left;
                }
                &:last-of-type {
                    text-align: right;
                }
            }

            th {
                padding: 3px 10px;
                border-top: 1px solid #787B89;
                border-bottom: 1px solid #787B89;
            }

            td {
                padding: 4px 10px;
            
                .icon-container {
                    display: inline-block;
                    width: 20px;
                    height: 12px;
                    line-height: 16px;
                }
                &.date {
                    color: #787B89;
                }
                &.details, &.index-type {
                    padding-top: 8px;
                    text-align: left;
                }

                &.details {
                    color: #787B89;
                    div {
                        padding: 2px 0;
                    }

                    .price-difference {
                        .previous-amount {
                            vertical-align: bottom;
                            display: inline-block;
                            width: 80px;
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;
                        }
                        i {
                            font-weight: bold;
                            margin-right: 5px;

                            &:last-of-type {
                                margin-right: 10px;
                            }
                        }
                    }

                    .progress-bar-container {
                        margin-top: 3px;
                        width: 100%;
                        height: 6px;
                        background-color: #3b3f4d;
                        padding: 0;
                        .progress-bar {
                            height: 2px;
                            background-color: #04affe;
                        }
                    }

                    .details-split {
                        display: flex;
                        justify-content: space-between;
                        .end-time {
                            padding-right: 10px;
                        }
                    }
                }

                &.time-remaining {
                    i {
                        margin-left: 6px;
                    }
                }

                .direction {
                    display: inline-block;
                    width: 35px;
                    padding-right: 8px;
                    text-align: right;
                }
            }
        }
    }
</style>