        <div class="trade-header">
            <div class="index">Bitcoin Index</div>
            <div>
                <el-select :placeholder="$t('main.historyPage.SelectTime')" v-model="timePeriod" class="select-styled">
                    <el-option
                            v-for="(item, i) in options"
                            :key="i"
                            :label="item.name"
                            :value="item.value">
                    </el-option>
                </el-select>
            </div>
        </div>
        <tradeTable 
            :tradeItems="orderDetails" :history="true">
        </tradeTable>
        <placeholder v-if="!(orderDetails && orderDetails.length)" :icon="'el-icon-document'" 
            :message="'main.OrderRecord.NoTrades'" 
