                    previousAmount: '1234.5412',
                    newAmount: '1233.2432',
                    time: '2.5m',
                    holdTime: '00:00:29',
                    timeRemaining: '00.00.12',
                    start: '10:12:00',
                    end: '10:13:00',
                    ID: '1234123910491029381091',
