        },
        changeLangEvent() {
            if (this.lang === "zh") {
                this.lang = "en";
            } else {
                this.lang = "zh";
            }
            this.$i18n.locale = this.lang;
        },
        routerPath(route) {
            this.$router.push(route);
        }
