<style lang="scss" scoped>
    .trade-history-page {
        width: 600px;
        margin: auto;
        .trade-header {
            color: #DDDEE5;
            font-size: 18px;
            display: flex;
            justify-content: space-between;
            padding: 15px 10px 0;
