        <div class="Carousel_map">
            <el-carousel :interval="5000" arrow="always" height="calc(120px + 20vw)">
                <el-carousel-item>
                    <div class="img-container banner-0">
                        <div class="banner-body">
                            <div class="banner-text left-align">
                                <h1>Left-align Sample</h1>This is sample text that fills up the screen with information.
                                This is sample text that fills up the screen with information.
                                This is sample text that fills up the screen with information.
                                This is sample text that fills up the screen with information.
                            </div>
                        </div>
                    </div>
                </el-carousel-item>
                <el-carousel-item>
                    <div class="img-container banner-1">
                        <div class="banner-body">
                            <div class="banner-text left-align">
                                <h1>Left-align Sample 2</h1>This is sample text that fills up the screen with information.
                                This is sample text that fills up the screen with information.
                                This is sample text that fills up the screen with information.
                                This is sample text that fills up the screen with information.
                            </div>
                        </div>
                    </div>
                </el-carousel-item>
                <el-carousel-item>
                    <div class="img-container banner-2">
                        <div class="banner-body">
                            <div class="banner-text right-align">
                                <h1>Right Align Sample</h1>This is sample text that fills up the screen with information.
                                This is sample text that fills up the screen with information.
                                This is sample text that fills up the screen with information.
                                This is sample text that fills up the screen with information.
                            </div>
                        </div>
                    </div>
                </el-carousel-item>
                <el-carousel-item>
                    <div class="img-container banner-2">
                        <div class="banner-body">
                            <div class="banner-text">
                                <h1>Center Align Sample</h1>This is sample text that fills up the screen with information.
                                This is sample text that fills up the screen with information.
                                This is sample text that fills up the screen with information.
                                This is sample text that fills up the screen with information.
                            </div>
                        </div>
                    </div>
                </el-carousel-item>
            </el-carousel>
        </div>
        <div class="Currency">
            <div class="Currency_top flex_c_c">
                <div class="Currency_top_left">
                    <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
                        <el-tab-pane label="收藏" name="Collection"></el-tab-pane>
                        <el-tab-pane label="大宗商品" name="Commodity"></el-tab-pane>
                        <el-tab-pane label="指数" name="index"></el-tab-pane>
                        <el-tab-pane label="股票" name="stock"></el-tab-pane>
                    </el-tabs>
                </div>
                <div class="Currency_top_right">
                    <el-input placeholder="Search" suffix-icon="el-icon-search" v-model="input2"></el-input>
                </div>
            </div>
            <div class="Datasheets">
                <GeneralPurpose :tableData="tableData" @getData="getData" />
            </div>
        </div>
        <Tutorial/>
    </div>
</template>
<script>
import GeneralPurpose from "@/components/generalPurpose";
import Tutorial from "@/components/Tutorial"
export default {
    name: "homeSection",
    props: {},
    data() {
        return {
            isShow:false,
            isGetData: false,
            tableData: [],
            input2: "",
            activeName: "Collection",
            transformIconAjaxData: {
                collection: [],
                product: [],
                index: [],
                stock: []
            },
            collecList: [],
            dataList: []
        };
    },
    computed: {},
    created() {
        this.getData();
        
        
    },
    mounted() {
        introJs().setOptions({
            nextLabel: "下一步",
            prevLabel: "上一步",
            skipLabel: "跳过",
            doneLabel: "结束"
        }).oncomplete(function () {
            //点击跳过按钮后执行的事件
        }).onexit(function () {
            //点击结束按钮后， 执行的事件
        }).start();
    },
    watch: {},
    methods: {
        getData(indexs) {
            let tab_index = {
                index: indexs || 0
            };
            this.$http.currencyForm().then(res => {
                this.isGetData = true;
                let result = res.data.data;
                this.dataList = result;
                result.forEach(msg => {
                    if (!msg.collec) {
                        msg.collec = 0;
                    }
                });
                this.transformIconAjaxData.product = result.filter(
                    v => v.market === "fx"
                );
                this.transformIconAjaxData.index = result.filter(
                    v => v.market === "1"
                );
                this.transformIconAjaxData.stock = result.filter(
                    v => v.market === "2"
                );
                let collecList = [];
                let token = this.$common.getCookie("token");
                if (!token) {
                    return false;
                }
                if(this.$common.getCookie('dialogVisible')){
                    this.dialogVisible = true;
                }
                this.$http.userDetails().then(res => {
                    if (res) {
                        collecList = res.data.data.favorite;
                        this.dataList.forEach(item => {
                            collecList.forEach(items => {
                                if (item.code == items.code) {
                                    item.collec = items.status;
                                }
                            });
                        });
                        this.transformIconAjaxData.collection = this.dataList.filter(
                            v => v.collec == 1
                        );
                        this.handleClick(tab_index);
                    }
                });
            });
        },
        handleClick(e) {
            let index = e.index;
            let data = this.transformIconAjaxData;
            if (this.activeName == "Collection") {
                index = 0;
            }
            if (index == 0) {
                this.tableData = data.collection;
            } else if (index == 1) {
                this.tableData = data.product;
            } else if (index == 2) {
                this.tableData = data.index;
            } else if (index == 3) {
                this.tableData = data.stock;
            }
        }
    },
    components: {
        GeneralPurpose,
        Tutorial
    }
};
</script>

<style  lang="scss">
