 * @LastEditors: wangxi
 * @LastEditTime: 2019-12-06 19:06:44
 -->
<template>
    <div class="orders-page">
        <tradeTable 
            :tradeItems="orderDetails">
        </tradeTable>
        <placeholder v-if="!(orderDetails && orderDetails.length)" :icon="'el-icon-s-data'" 
            :message="'main.OrderRecord.NoCurrentOrders'"
            :buttonProps="{label: 'main.OrderRecord.StartTrading', path: 'transactionPage'}"></placeholder>
    </div>
</template>

<script>
import { mapState } from "vuex";
import tradeTable from '@/components/tradeTable'
import placeholder from '@/components/placeholder'
import SockJS from "sockjs-client";
import Stomp from "stompjs";
export default {
    name: "ordersPage",
    data() {
        return {
            lang: "zh",
            orderDetails: [],
            stompClient: null
        };
    },
    computed: {
        ...mapState({
            allIconNowPrice: state => state.allIconNowPrice
        })
    },
    created() {
        this.lang = this.$i18n.locale;
        this.getUserOrders()
    },
    mounted() {
    },
    components:  {
        tradeTable,
        placeholder
    },
    methods: {
        getUserOrders (){
            this.orderDetails = [
                {
                    marketCode: 'Bitcoin Index',
                    createTime: '3/19/2019',
                    tradePrice: 3000,
                    marketPrice: '1234.5412',
                    nowPrice: '1233.2432',
                    time: '1m',
                    timeRemaining: '00.00.12',
                    tradeTime: '10:12:00',
                    end: '10:13:00',
                    tradeId: '1234123910491029381091',
                    percentTimeLeft: .87
                },
                {
                    index: 'Gold',
                    date: '3/19/2019',
                    position: 3000,
                    previousAmount: '1211.5330',
                    newAmount: '1264.1111',
                    timeRemaining: '00.00.12',
                    time: '2.5m',
                    start: '10:12:00',
                    end: '10:13:00',
                    ID: '1234123910491029381092',
                    percentTimeLeft: .41
                }
            ];
            // 获取持仓订单数据
            let token = this.$common.getCookie("token");
            if (!token) {
                return false;
            }
            this.$http.getOrderData().then(res => {
                if (res) {
                    let arr = res.data.data;
                    // 持仓数据存在并且没有websocket连接，就重新连接，获取实时价格
                    if (arr.holdingList.length && !this.stompClient) {
                        this.initSocketJs();
                    }
                    // 后端没按最新下单时间返回，前端排序一下
                    arr.holdingList.reverse();
                    // 现价占个坑
                    arr.holdingList.forEach(v => v.nowPrice = null)
                    this.orderDetails = arr.holdingList
                    // 把lastMinuteList存起来，用于图表结果展示
                    this.$store.commit(
                        "setLastMinuteList",
                        res.data.data.lastMinuteList
                    );
                }
            });
        },
        initSocketJs() {
            this.connection();
        },
        connection() {
            // 建立连接对象
            let socket = new SockJS(this.$base.websocketUri);
            // 获取STOMP子协议的客户端对象
            this.stompClient = Stomp.over(socket);
            this.stompClient.debug = null;
            this.stompClient.connect(
                {},
                data => {
                    this.stompClient.subscribe("/pic/all", response => {
                        this.$store.commit(
                            "setAllIconNowPrice",
                            JSON.parse(response.body)
                        );
                        this.orderDetails.forEach(v => {
                            if (this.allIconNowPrice) {
                                v.nowPrice = this.allIconNowPrice[v.marketCode];
                            }
                        });
                    });
                },
                err => {
                    // 连接发生错误时的处理函数
                    console.log("失败");
                    console.log(err);
                }
            );
        },
        disconnect() {
            if (this.stompClient) {
                this.stompClient.disconnect();
            }
        },
        changeLangEvent() {
            if (this.lang === "zh") {
                this.lang = "en";
            } else {
                this.lang = "zh";
            }
            this.$i18n.locale = this.lang;
        },
        routerPath(route) {
            this.$router.push(route);
        }
    },
    beforeDestroy (){
        this.disconnect();
    }
};
</script>


<style lang="scss" scoped>
    .orders-page {
        width: 600px;
        margin: auto;
        
        @media screen and (max-width: 768px) {
            width: 100%;
            table {
                margin-top: 0;
            }
        }
    }
</style>